.Rating{
	.rating-item-box,.rating-item-box-ok{
		position: relative;
		margin: 0 4px 0;
	}	
	.rating-item-box-ok{		
		z-index: 1;
	}

	.icon-text,.icon-text-ok{
		position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 18px;
    font-weight: 700;
	}
	.icon-text-ok{		
    color: #ffffff;
	}
	.icon{
		width: 60px;
	}
}