.HotelCard{
	.main-content{
		display: flex;
		border: 1px solid #666666;
	}	
	.detail-content{
		display: none;
		&.opened{
			display: block;
		}
	}
}
.shot-info-container{
	flex: 1 1 500px;
	padding: 30px;
}
.hotel-name{
	font-size: 24px;
	font-weight: 700;
	text-transform: capitalize;
}
.hotel-rate-title{
	font-size: 18px;
	font-weight: 700;	
}

.action-container{
	padding: 15px;
	flex: 0 1 200px;
	display: flex;
  justify-content: center;
  align-items: flex-end;
}

.detail-button{
	display: inline-block;
	min-width: 150px;
	height: 60px;
	background-color: #f55;
	border: none;
	outline: 0;
	cursor: pointer;

	font-size: 18px;
	font-weight: 700;
	text-transform: capitalize;
	transition: all ease 0.5s;
}
.detail-button:hover{
	background-color: #a00;
}
.rate-stars-container{	
	display: flex;
}

.price-container{	
	display: flex;
}