.Sidebar{
	position: fixed;
	z-index: 1;
	width: $sidebar-width;
    height: 100vh;
    left: 0;
    top: 0;	
    padding: 50px 30px;
	//background-color: #888888;
	box-shadow: 0px 1px 2px 0 rgba(0, 0, 0, 0.19);
	background: url('../img/pahel-336436.jpg') no-repeat 50% 50% / cover;
	//background: linear-gradient(to bottom, #FF5580, #ececec);
	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-color: rgba(#ffffff,0.8);
	}
}
