}
.filter-section{	
	&:not(:first-child){		
		padding: 40px 0 20px;
  	border-bottom: 1px solid #666666;
	}  
	&:first-child{
		padding: 0 0 20px;
  	border-bottom: 1px solid #666666;
	}
	&:not(:last-child){		
